import React from "react";
import { useCurrencyStore } from "../../store/currencyStore";
import { useShoppingCartStore } from "../../store/shoopingCartStore";
import { fixedPrice } from "../../utils";
import { TotalPriceStyle } from "./style";

const TotalPrice = () => {
    const { totalPrice } =useShoppingCartStore()
    const { currency } = useCurrencyStore();
  return (
    <TotalPriceStyle>
      Total : {currency.symbol}
      {fixedPrice(totalPrice * currency.rate)}
    </TotalPriceStyle>
  );
};

export default TotalPrice;
