import React, { useCallback } from "react";
import { Link } from "react-router-dom";
import { useShoppingCartStore } from "../../store/shoopingCartStore";
import ShoppingCartProduct from "./ShoppingCartProduct";
import { Main } from "./style";
import TotalPrice from "./TotalPrice";

const ShoppingCart = () => {
  //fucc for cart buttons
  const { removeFromCart, addToCart,removeItemAll, sc } = useShoppingCartStore();
  const addMemoized = useCallback(addToCart, []);
  const removeMemoized = useCallback(removeFromCart, []);
  const removeItemAllMemo = useCallback(removeItemAll, []);

  const combineCart = () => {
    if (sc.length) {
      let updateState = [];
      const shoppingCartMap = new Map();
      sc.forEach((e) => {
        let item = shoppingCartMap.get(e) || { code: e, count: 0 };
        item.count++;
        shoppingCartMap.set(item.code, item);
      });
      for (const [key, value] of shoppingCartMap.entries()) {
        updateState.push(value);
      }
      return updateState;
    }
  };
  return (
    <Main empty={sc.length === 0}>
      {sc.length === 0 ? (
        <>
          <h2>Your Cart Is Empty</h2>

          <Link to="/gallery">
            {" "}
            <h1>Go To Gallery</h1>
          </Link>
        </>
      ) : (
        <>
          {combineCart()?.map((p, idx) => {
            return (
              <ShoppingCartProduct
                addMemoized={addMemoized}
                removeMemoized={removeMemoized}
                removeItemAllMemo={removeItemAllMemo}
                key={`${p.code}`}
                id={p.code}
                amount={p.count}
              />
            );
          })}
          <TotalPrice />
        </>
      )}
    </Main> 
  );
};

export default ShoppingCart;
